<template>
  <div class="pay">
    <van-nav-bar
      left-text="返回"
      title="提交订单"
      left-arrow
      @click-left="back"
    />
    <div class="address-box">
      <!-- <div class="title" @click="showAddressList">选择收货地址</div> -->
      <van-cell class="title" title="选择收货地址" is-link @click="showAddressList"/>
      <div class="clearfix">
        <div class="name fl">{{currentAddress.name}}</div>
        <div class="phone fl">{{currentAddress.tel}}</div>
      </div>
      <div class="address">{{currentAddress.address}}</div>
    </div>

    <!-- 商品订单 -->
    <div class="order-box">
      <div class="title">商品列表</div>
      <div class="order-list">

        <div class="item" v-for="(item,index) in orderData" :key="index">
          <div class="item-left">
            <div class="name">{{item.name}}</div>
            <div class="rule">{{item.rule}}</div>
          </div>
          <div class="count">x{{item.count}}</div>
          <div class="price">￥{{item.price}}</div>
        </div>

      </div>
      <!-- 合计 -->
      <div class="order-total-box clearfix">
        <div class="order-count fl">共计 {{count}} 件商品</div>
        <!-- <div class="order-price fr">合计￥{{totalPrice}}</div> -->
      </div>
    </div>

    <!-- 弹出地址 -->
    <van-popup v-model="show" closeable round position="bottom" :style="{ height: '60%' }" >
      <van-address-list
        v-model="chosenAddressId"
        :list="list"
        default-tag-text="默认"
        @add="onAdd"
        @select="select"
      />
    </van-popup>

    <van-submit-bar :price="totalPrice" button-text="去支付" @submit="pay"/>
  </div>

</template>

<script>
  import '../../src/assets/less/pay.less'
import { utils } from '../assets/js/utils';
  export default {
    data(){
      return{
        orderData: [],
        show: false,
        count: 0,
        totalPrice: 0,
        sids: '',

        chosenAddressId: '',
        list: [],

        currentAddress: {
          id: '',
          name: '',
          tel: '',
          address: '',
        }
        
      }
    },
    created(){
      this.sids = this.$route.query.sids.split('-');
      // // console.log(this.sids)
      this.getOrderProduct();
      this.CountTotalPrice();
      this.getAddressData();
    },
    methods:{
      back(){
        this.$router.go(-1);
      },
      // 获取订单列表数据
      getOrderProduct(){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          // 跳转到登录页
          this.$router.push({name: 'Login'});
          return;
        }
        this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
        });

        this.axios({
          method: 'GET',
          url:'/commitShopcart',
          params:{
            appkey: this.appkey,
            tokenString: tk,
            sids: JSON.stringify(this.sids)
          }
        }).then(result => {
          if(result.data.code == 50000){
            this.$toast.clear()
            this.orderData = result.data.result;
            this.CountTotalPrice();
          }
          // console.log("findAllShopcart result ==> ",result)
        }).catch(err =>{
          this.$toast.clear()
          // console.log("findAllShopcart result ==> ",err)
        })

      },
      // 弹出收货地址选择
      showAddressList(){
        this.show = true;
      },
      // 添加地址
      onAdd(){
        this.$router.push({name: 'AddressEdit'})
      },
      // 计算总价
      CountTotalPrice(){
        this.totalPrice = 0;
        this.orderData.map(v =>{
          this.totalPrice += v.count * v.price;
          this.count += v.count;
        })
        this.totalPrice *= 100;
        // console.log(this.totalPrice)
      },
      // 获取地址数据
      getAddressData(){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          // 跳转到登录页
          this.$router.push({name: 'Login'});
          return;
        }
        this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
        });

        this.axios({
          method: 'GET',
          url:'/findAddress',
          params:{
            appkey: this.appkey,
            tokenString: tk,
          }
        }).then(result => {
          if(result.data.code == 20000){
            this.$toast.clear()
            result.data.result.map( v => {
              let item = {
                id: v.aid,
                name: v.name,
                tel: v.tel,
                address: v.province + v.city + v.county + v.addressDetail,
                isDefault: Boolean(v.isDefault),
              };
              this.list.push(item)

              if(v.isDefault){
                this.chosenAddressId = v.aid;
                for(let key in this.currentAddress){
                  this.currentAddress[key] = item[key];
                }
              }
            })
          }
          // console.log("findAddress result ==> ",result)
        }).catch(err =>{
          this.$toast.clear()
          // console.log("findAddress result ==> ",err)
        })

      },
      // 切换选择
      select(item){
        // // console.log(item)
        for(let key in this.currentAddress){
          this.currentAddress[key] = item[key];
        }
        this.show = false;
      },
      // 支付
      pay(){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }

        // 判断是否填写地址信息
        if(this.chosenAddressId == ''){
          this.$toast("请选择地址信息");
          return;
        }

        this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
        });

        let data = utils.queryString({
          appkey: this.appkey,
          tokenString: tk,
          sids: JSON.stringify(this.sids),
          phone: this.currentAddress.tel,
          address: this.currentAddress.address,
          receiver: this.currentAddress.name
        })

        this.axios({
          method: 'POST',
          url:'/pay',
          data
        }).then(result => {
          this.$toast.clear();

          if(result.data.code == 60000){
            this.$toast.success(result.data.msg)
            setTimeout(() => {
              this.$router.push({name: 'Order'})
            }, 2000);
          }else{
            this.$toast.fail(result.data.msg)
          }
          // console.log("pay result ==> ",result)
        }).catch(err =>{
          this.$toast.clear()
          // console.log("pay result ==> ",err)
        })      
      },

    },
  }
</script>

<style lang="less" scoped>

</style>